<template>
  <div class="">
    <el-card style="margin-top:50px;">
      <!-- 面包屑导航区域 -->
      <el-breadcrumb
        separator-class="el-icon-arrow-right"
        style="margin-bottom: 20px"
      >
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      </el-breadcrumb>
      <el-input
        v-model="queryInfo.search"
        class="search-input"
        suffix-icon="el-icon-search"
        placeholder="请输入用户地址查询信息"
        @change="search"
        style="width: 368px"
      ></el-input>
      <el-button
        type="primary"
        icon="el-icon-coin"
        style="margin-left: 50px"
        @click="showTransfer"
        >充币</el-button
      >
      <el-button type="primary" icon="el-icon-coin" @click="showTakeOf"
        >提币</el-button
      >
      <el-card
        v-if="flag && this.queryInfo.search.length === 42"
        style="margin-top: 20px; padding-right: 180px"
      >
        <h2 style="margin-bottom: 20px">用户团队信息</h2>
        <div class="">
          <ul class="t1">
            <li>{{ group[0] }}: {{ groupInfo.amount }} USDT</li>
            <li>{{ group[1] }}: {{ groupInfo.level }} 级</li>
            <li>{{ group[2] }}: {{ groupInfo.award }} USDT</li>
          </ul>
        </div>
        <div class="box">
          <div class="">
            <ul v-for="(item, index) in list[1]" :key="index">
              <li>
                团员{{ index + 1 }}地址: <span class="color1">{{ item }}</span>
              </li>
            </ul>
          </div>
          <div class="">
            <ul>
              <li v-if="list[1].length > 0">
                团员1认购: <span class="color2">{{ amount[0] }}</span> USDT
              </li>
              <li v-if="list[1].length > 1">
                团员2认购: <span class="color2">{{ amount[1] }}</span> USDT
              </li>
              <li v-if="list[1].length > 2">
                团员3认购: <span class="color2">{{ amount[2] }}</span> USDT
              </li>
            </ul>
          </div>
        </div>
      </el-card>
    </el-card>
    <!-- 充币对话框 -->
    <el-dialog
      title="充币"
      :visible.sync="dialogVisible1"
      width="500px"
      @close="dialogClosed1"
    >
      <!-- 内容主体区域 -->
      <el-form :model="form1" label-width="80px" ref="refForm1" :rules="rules">
        <el-form-item label="充币地址" prop="address">
          <el-input v-model="form1.address"></el-input>
        </el-form-item>
        <el-form-item label="充币金额" prop="count">
          <el-input v-model="form1.count"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible1 = false">取 消</el-button>
        <el-button type="primary" @click="confirm1">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 提币对话框 -->
    <el-dialog
      title="提币"
      :visible.sync="dialogVisible2"
      width="500px"
      @close="dialogClosed2"
    >
      <!-- 内容主体区域 -->
      <el-form :model="form2" label-width="80px" ref="refForm2" :rules="rules">
        <el-form-item label="提币地址" prop="address">
          <el-input v-model="form2.address"></el-input>
        </el-form-item>
        <el-form-item label="提币金额" prop="count">
          <el-input v-model="form2.count"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible2 = false">取 消</el-button>
        <el-button type="primary" @click="confirm2">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Utils from "../utils";
export default {
  data() {
    return {
      contract: {},
      erc: {},
      queryInfo: {
        search: "",
        search2: "",
        search3: "",
      },
      list: [],
      group: ["团队认购总量", "团队等级", "团队奖励"],
      groupInfo: {
        amount: 0,
        level: 0,
        award: 0,
      },
      amount: [],
      flag: false,
      form1: {
        address: "0x8F5715B7e6F00282a49e309D0fFf27DcdE4c6ca0",
        count: "",
      },
      dialogVisible1: false,
      form2: {
        address: "",
        count: "",
      },
      dialogVisible2: false,
      rules: {
        address: [{ required: true, message: "请输入地址", trigger: "blur" }],
        count: [{ required: true, message: "请输入金额", trigger: "blur" }],
      },
    };
  },
  methods: {
    search() {
      this.list = [];
      this.groupInfo = {};
      this.amount = [];
      this.flag = false;
      this.contract.getTeamStats(this.queryInfo.search, (err, res) => {
        if (!err) {
          console.log(res, "getTeamStats", this.queryInfo.search);
          this.list = res;
          this.groupInfo.amount = res[0][0].c[0] / 1000000;
          this.groupInfo.level = res[0][1].c[0];
          this.groupInfo.award = res[0][2].c[0] / 1000000;
          res[2].map((item, index) => {
            this.amount.push(item.c[0] / 1000000);
          });
          this.flag = true;
          console.log(this.amount, this.list);
        } else {
          console.log(err);
        }
      });
    },
    dialogClosed1() {
      this.$refs.refForm1.resetFields();
    },
    dialogClosed2() {
      this.$refs.refForm2.resetFields();
    },
    showTransfer() {
      this.dialogVisible1 = true;
    },
    showTakeOf() {
      this.dialogVisible2 = true;
    },
    confirm1() {
      this.erc.transfer(
        this.form1.address,
        this.form1.count * 1000000,
        (err, res) => {
          if (!err) {
            console.log(res, "transfer");
            this.dialogVisible1 = false;
            this.$message.success("充币成功");
          } else {
            console.log(err);
            this.$message.error("充币失败");
          }
        }
      );
    },
    confirm2() {
      this.$refs.refForm2.validate((valid) => {
        if (valid) {
          this.contract.takeOf(
            this.form2.address,
            this.form2.count * 1000000,
            (err, res) => {
              if (!err) {
                console.log(res, "transfer");
                this.dialogVisible2 = false;
                this.$message.success("提币成功");
              } else {
                console.log(err);
                this.$message.error("提币失败");
              }
            }
          );
        } else {
          this.$message.error("请正确填写表单");
        }
      });
    },
  },
  created() {
    Utils.instance().then((res) => {
      // console.log(res, "11111");
      this.contract = res;
    });
    Utils.ercInstance().then((res) => {
      // console.log(res, "11111");
      this.erc = res;
    });
  },
};
</script>
<style lang="scss" scoped>
.t1 {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  font-size: 18px;
  font-weight: 600;
  width: 700px;
}
.box {
  display: flex;
  justify-content: space-between;
  div {
    ul {
      li {
        margin-bottom: 5px;
        .color1 {
          color: #42bd87;
        }
        .color2 {
          color: blue;
        }
      }
    }
  }
}
</style>