<template>
  <div class="">
    <el-card style="margin-top: 50px">
      <!-- 面包屑导航区域 -->
      <el-breadcrumb
        separator-class="el-icon-arrow-right"
        style="margin-bottom: 20px"
      >
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>资金池列表</el-breadcrumb-item>
      </el-breadcrumb>
      <el-table :data="list" border stripe>
        <el-table-column
          align="center"
          type="index"
          width="50px"
        ></el-table-column>
        <el-table-column label="总入金(USDT)" prop="amount" align="center">
        </el-table-column>
        <el-table-column label="余额(USDT)" prop="balance" align="center">
        </el-table-column>
        <el-table-column
          label="当日需拨出资金(USDT)"
          prop="todayNeed"
          align="center"
        >
        </el-table-column>
        <el-table-column
          label="次日需拨出资金(USDT)"
          prop="tomNeed"
          align="center"
        >
        </el-table-column>
      </el-table>
      <el-table :data="list2" border stripe style="margin-top: 50px">
        <el-table-column
          align="center"
          type="index"
          width="50px"
        ></el-table-column>
        <el-table-column
          label="矿机1参与人数"
          prop="num1"
          align="center"
        ></el-table-column>
        <el-table-column
          label="矿机2参与人数"
          prop="num2"
          align="center"
        ></el-table-column>
        <el-table-column
          label="矿机3参与人数"
          prop="num3"
          align="center"
        ></el-table-column>
        <el-table-column
          label="矿机4参与人数"
          prop="num4"
          align="center"
        ></el-table-column>
        <el-table-column
          label="矿机5参与人数"
          prop="num5"
          align="center"
        ></el-table-column>
        <el-table-column
          label="矿机6参与人数"
          prop="num6"
          align="center"
        ></el-table-column>
        <el-table-column
          label="矿机7参与人数"
          prop="num7"
          align="center"
        ></el-table-column>
        <el-table-column label="矿机8参与人数" prop="num8" align="center">
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
import Utils from "../utils";
export default {
  data() {
    return {
      contract: {},
      // list:[{
      //     withdrawals:0,
      //     withdrawFee:0,
      //     withdrawLimit:0,
      // }],
      list: [
        {
          amount: 0,
          balance: 0,
          todayNeed: 0,
          tomNeed: 0,
        },
      ],
      list2: [
        {
          num1: 0,
          num2: 0,
          num3: 0,
          num4: 0,
          num5: 0,
          num6: 0,
          num7: 0,
          num8: 0,
        },
      ],
    };
  },
  methods: {
    showEdit() {},
    getList() {
      this.contract.overview((err, res) => {
        if (!err) {
          console.log(res, "overview");
          this.list[0].amount = res[0][0].c[0];
          if ((res[0][1].c.length === 2)) {
            this.list[0].balance = res[0][1].c[0] + "" + (res[0][1].c[1] + "");
          } else {
            this.list[0].balance = res[0][1].c[0];
          }
          this.list[0].todayNeed = res[0][2].c[0];
          this.list[0].tomNeed = res[0][3].c[0];
          console.log(this.list, "list" , res);
          this.list2[0].num1 = res[1][1].c[0];
          this.list2[0].num2 = res[1][2].c[0];
          this.list2[0].num3 = res[1][3].c[0];
          this.list2[0].num4 = res[1][4].c[0];
          this.list2[0].num5 = res[1][5].c[0];
          this.list2[0].num6 = res[1][6].c[0];
          this.list2[0].num7 = res[1][7].c[0];
          this.list2[0].num8 = res[1][8].c[0];
          console.log(this.list2, "list2");
        } else {
          console.log(err, "overview");
        }
      });
    },
  },
  created() {
    Utils.instance().then((res) => {
      // console.log(res, "11111");
      this.contract = res;
      this.getList();
    });
  },
};
</script>
