<template>
  <div class="">
    <el-card style="margin-top: 50px">
      <!-- 面包屑导航区域 -->
      <el-breadcrumb
        separator-class="el-icon-arrow-right"
        style="margin-bottom: 20px"
      >
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>提现管理</el-breadcrumb-item>
      </el-breadcrumb>
      <el-table :data="list" border stripe>
        <el-table-column
          align="center"
          type="index"
          width="50px"
        ></el-table-column>
        <el-table-column label="提现次数" prop="withdrawals" align="center">
        </el-table-column>
        <el-table-column
          label="提现手续费(USDT)"
          prop="withdrawFee"
          align="center"
        >
          <template v-slot="scope">
            {{ scope.row.withdrawFee / 1000000 }}
          </template>
        </el-table-column>
        <el-table-column
          label="提现门槛(USDT)"
          prop="withdrawLimit"
          align="center"
        >
          <template v-slot="scope">
            {{ scope.row.withdrawLimit / 1000000 }}
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template v-slot="scope">
            <el-button
              type="warning"
              icon="el-icon-edit"
              size="mini"
              @click="showEdit(scope.row)"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- 修改提现参数的对话框 -->
    <el-dialog
      title="修改提现参数"
      :visible.sync="editDialogVisible"
      width="30%"
      @close="editDialogClosed"
    >
      <!-- 内容主体区域 -->
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="提现次数" prop="withdrawals">
          <el-input
            v-model="editForm.withdrawals"
            @input="change($event)"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="矿机名称" prop="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item> -->
        <el-form-item label="提现手续费" prop="withdrawFee">
          <el-input
            v-model="editForm.withdrawFee"
            @input="change($event)"
          ></el-input>
        </el-form-item>
        <el-form-item label="提现门槛" prop="withdrawLimit">
          <el-input
            v-model="editForm.withdrawLimit"
            @input="change($event)"
          ></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editConfirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Utils from "../utils";
export default {
  data() {
    return {
      contract: {},
      list: [
        {
          withdrawals: 0,
          withdrawFee: 0,
          withdrawLimit: 0,
        },
      ],
      editDialogVisible: false,
      editForm: {
        // withdrawals: 0,
        // withdrawFee: 0,
        // withdrawLimit: 0,
      },
      editFormRules: {},
    };
  },
  methods: {
    change() {
      this.$forceUpdate();
    },
    editConfirm() {
      this.contract.setWithdrawals(
        this.editForm.withdrawals,
        this.editForm.withdrawFee * 1000000,
        this.editForm.withdrawLimit * 1000000,
        (err, res) => {
          if (!err) {
            this.$message.success("操作成功");
            this.editDialogClosed();
            console.log(res, "setWithdrawals", "success");
          } else {
            this.$message.error("操作失败");
            console.log(err, "setWithdrawals");
          }
        }
      );
    },
    editDialogClosed() {
      this.editDialogVisible = false;
      this.getList();
    },
    showEdit(row) {
      this.editDialogVisible = true;
      this.editForm.withdrawals = row.withdrawals;
      this.editForm.withdrawFee = row.withdrawFee / 1000000;
      this.editForm.withdrawLimit = row.withdrawLimit / 1000000;
    },
    getList() {
      this.contract.getWithdrawals((err, res) => {
        if (!err) {
          console.log(res, "getWithdrawals");
          this.list[0].withdrawals = res[0].c[0];
          this.list[0].withdrawFee = res[1].c[0];
          this.list[0].withdrawLimit = res[2].c[0];
        } else {
          console.log(err, "getWithdrawals");
        }
      });
    },
  },
  created() {
    Utils.instance().then((res) => {
      this.contract = res;
      this.getList();
    });
  },
};
</script>
<style lang="scss" scoped>
</style>