<template>
    <div class="">
    <el-card style="margin-top:50px;">
      <el-row>
            <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right" style="float:left;">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>矿机管理</el-breadcrumb-item>
    </el-breadcrumb>
          <!-- <el-input
            v-model="millLev"
            class="search-input"
            suffix-icon="el-icon-search"
            placeholder="请输入矿机等级查询(1~8)"
            @change="search1"
            style= "width:330px;"
          ></el-input>
          <el-button type="warning" icon="el-icon-edit" size="mini" @click="getList" style="margin-left: 100px">修改矿机</el-button> -->
          <p style="float: right;margin-bottom: 10px;">矿机类型总数:{{lev}}种</p>
      </el-row>
      <el-table :data="list" border stripe>
        <el-table-column align="center" type="index" label="等级" width="50px"></el-table-column>
            <!-- <template v-slot="scope">
                <span class="addressText" @click="nextTable(scope.row)">{{scope.row.address}}</span>
            </template> -->
        <el-table-column label="矿机名称" prop="name" align="center">
          <template v-slot='scope'>
            {{scope.row.name | nameFormat}}
          </template>
        </el-table-column>
        <el-table-column label="矿机价格(USDT)" prop="price" align="center">
          <template v-slot='scope'>
            {{scope.row.price/1000000}}
          </template>
        </el-table-column>
        <el-table-column label="矿机数量" prop="num" align="center">
        </el-table-column>
        <el-table-column label="矿机周期(月)" prop="tenancy" align="center">
          <template v-slot='scope'>
            <!-- {{scope.row.tenancy/2592000}} -->
            {{scope.row.tenancy/150}}
          </template>
        </el-table-column>
        <el-table-column label="日化收益(%)" prop="earn" align="center">
          <template v-slot='scope'>
            {{scope.row.earn/10}}
          </template>
        </el-table-column>
        <el-table-column label="修改矿机" prop="address" align="center">
          <template v-slot="scope">
            <el-button type="warning" icon="el-icon-edit" size="mini" @click="showEdit(scope.row)">编辑</el-button>
          </template>
        </el-table-column>
      </el-table>
          <!-- 修改矿机的对话框 -->
    <el-dialog title="修改矿机" :visible.sync="editDialogVisible" width="30%" @close="editDialogClosed">
      <!-- 内容主体区域 -->
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="80px">
        <el-form-item label="矿机等级" prop="level">
          <el-input v-model="editForm.level" disabled></el-input>
        </el-form-item>
        <!-- <el-form-item label="矿机名称" prop="name">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item> -->
        <el-form-item label="矿机数量" prop="num">
          <el-input v-model="editForm.num"></el-input>
        </el-form-item>
        <el-form-item label="日化收益" prop="earn">
          <el-input v-model="editForm.earn"></el-input>
        </el-form-item>
        <!-- <el-form-item label="周期" prop="tenancy">
          <el-input v-model="editForm.tenancy"></el-input>
        </el-form-item> -->
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editConfirm">确 定</el-button>
      </span>
    </el-dialog>
        </el-card>
    </div>
</template>
<script>
import Utils from '../utils';
export default {
    data() {
        return {
            contract:{},
            list: [],
            editForm : {
            price:0,
            num:0,
            earn:0,
            tenancy:0,
            name:'',
            level: 0
          },
            itemData: {},
            myContract: {},
            millLev: "",
            millInfo: [],
            flag1:false,
            lev: "",
            editDialogVisible: false,
            editFormRules:{
              name: [{ required: true, message: '请输入应用名', trigger: 'blur' }],
              num: [{ required: true, message: '请输入数量', trigger: 'blur' }],
              earn: [{ required: true, message: '请输入日化收益', trigger: 'blur' }],
              tenancy: [{ required: true, message: '请输入应用名', trigger: 'blur' }]
            }
        }
    },
    created() {
      // setTimeout(() => {
        Utils.instance().then(res =>{
          console.log(res, '11111')
          this.contract = res
          this.getList()
        })
      // }, 1000);
        // this.instance();
    },
    filters: {
      nameFormat(val) {
        const data = val.substr(2)
        if (data.length % 2) return "";
      var tmp = "";
      for (var i = 0; i < data.length; i += 2) {
        if (data.charAt(i) != 0 || data.charAt(i + 1) != 0)
          tmp += "%" + data.charAt(i) + data.charAt(i + 1);
      }
      return decodeURI(tmp);
        // const that = this
        // return this.f(val.substr(2))
      }
    },
    methods: {
          f(data) {
      if (data.length % 2) return "";
      var tmp = "";
      for (var i = 0; i < data.length; i += 2) {
        if (data.charAt(i) != 0 || data.charAt(i + 1) != 0)
          tmp += "%" + data.charAt(i) + data.charAt(i + 1);
      }
      return decodeURI(tmp);
    },
    
     getList() {
          console.log('getlist')
          this.contract.getMills((err,res)=>{
          if (!err) {
            this.lev = res[0].length - 1
            var that = this
            const list = []
            for (var i = 0; i < res[0].length; i++) { 
              list[i] = {
            price:0,
            num:0,
            earn:0,
            tenancy:0,
            name:'',
            level:i
            }
          }
            res[0].map((item,index)=> {
              list[index]['price'] = item.c[0]
            })
            res[1].map((item,index)=> {
              list[index].num = item.c[0]
            })
            res[2].map((item,index)=> {
              list[index].earn = item.c[0]
            })
            res[3].map((item,index)=> {
              list[index].tenancy = item.c[0]
            })
            res[4].map((item,index)=> {
              // const it = item.substr(2);
              // list[index].name = that.f(it)
              list[index].name = item
            })
            list.shift()
            that.list = list
            console.log(res, this.lev,'res list', list , that.list)
          } else {
            this.$message.error('获取列表失败!')
            console.log(err)
            }});
      },
      editDialogClosed() {
        this.getList()
      },
      editConfirm() {
        console.log(this.editForm, 'this.editForm')
        this.contract.setMill(
        this.editForm.level,
        this.editForm.price,
        this.editForm.num,
        this.editForm.earn*10,
        this.editForm.tenancy,
        [8, 4, 1],
        this.editForm.name,
        (err,res)=>{
          if (!err) {
            console.log(res, 'success')
            this.$message.success('修改成功')
            this.getList();
          } else {
            console.log(err)
            }
          })           
      },
      showEdit(row) {
        this.editForm = row
        this.editForm.earn = this.editForm.earn/10
        console.log(row, this.editForm)
        this.editDialogVisible = true
      }
    }
}
</script>
<style lang='scss' scoped>
.el-main{
  height: 100vh;
}
</style>